.nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 30px;
  padding: 20px;
  z-index: 1;

  display: flex;
  justify-content: space-between;

  transition-timing-function: ease-in-out;
  transition: all 0.5s;
}

.nav__black {
  background-color: #111;
  /* height: 30px; */
}

.nav__logo {
  position: fixed;
  left: 20px;
  width: 80px;
  object-fit: contain;
}

.nav__avatar {
  position: fixed;
  right: 20px;
  width: 30px;
  object-fit: contain;
}
